<!-- 角色管理 -->
<template>
  <div>
    <Row :gutter="20">
      <Col :sm="6" :lg="4">
        <Input v-model="queryForm.roleName" placeholder="角色"></Input>
      </Col>
      <Col :sm="6" :lg="4">
        <Button type="success" @click="page()">
          <Icon type="md-search" />查询
        </Button>
      </Col>
    </Row>
    <br />
    <Row>
      <Button type="success" @click="showRoleModal()">
        <Icon type="md-add" />添加
      </Button>
      <Button type="error" @click="showDelModal" class="margin-left">
        <Icon type="md-close" />删除
      </Button>
    </Row>
    <br />
    <Card>
      <Table :columns="column1" :data="data1" @on-selection-change="selectionChange"></Table>
      <br />
      <Page :total="dataTotal" show-total @on-change="page" />
    </Card>

    <!-- 对话框 -->
    <Modal v-model="roleModal" title="角色管理">
      <Form ref="roleForm" :model="roleForm" :rules="ruleValidate">
        <FormItem label="角色名称" prop="roleName">
          <Input v-model="roleForm.roleName"></Input>
        </FormItem>
        <FormItem label="类型" prop="roleType">
          <RadioGroup v-model="roleForm.roleType">
            <Radio :label="1">管理员</Radio>
            <Radio :label="2">普通用户</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="启用" prop="useState">
          <RadioGroup v-model="roleForm.useState">
            <Radio :label="1">是</Radio>
            <Radio :label="0">否</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="备注" prop="remark">
          <Input v-model="roleForm.remark"></Input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button @click="roleModal = false">关闭</Button>
        <Button type="success" @click="handleSubmit('roleForm')">保存</Button>
      </div>
    </Modal>
    <Modal v-model="permissionModal" title="设置权限">
      当前角色: 系统管理员
      <Checkbox>所有权限</Checkbox>
      <br />
      <Tree :data="permissionData" show-checkbox></Tree>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "WasteStorage",
  data() {
    return {
      barCode: "",
      roleModal: false,
      permissionModal: false,
      isAdd: true,
      selectionIds: "",
      queryForm: {
        pageIndex: 1,
        pageSize: 10,
        roleName: ""
      },
      dataTotal: 0,
      roleForm: {
        roleId: 0,
        roleName: "",
        roleType: 1,
        useState: 0,
        orgId: 1,
        remark: ""
      },
      ruleValidate: {
        roleName: [
          { required: true, message: "名称不能为空", trigger: "blur" }
        ],
        roleType: [
          {
            required: true,
            type: "number",
            message: "请选择角色类型",
            trigger: "blur"
          }
        ],
        useState: [
          {
            required: true,
            type: "number",
            message: "请选择是否启用",
            trigger: "blur"
          }
        ]
      },
      column1: [
        {
          key: "select",
          type: "selection",
          width: 60
        },
        {
          key: "roleName",
          title: "角色名"
        },
        {
          key: "roleType",
          title: "类型",
          render(h, params) {
            return h("span", params.row.roleType === 1 ? "管理员" : "普通用户");
          }
        },
        {
          key: "useState",
          title: "状态",
          render: (h, params) => {
            return h(
              "Tag",
              {
                props: {
                  color: params.row.useState === 1 ? "success" : "default"
                }
              },
              params.row.useState === 1 ? "已启用" : "未启用"
            );
          }
        },
        {
          key: "remark",
          title: "备注"
        },
        {
          key: "action",
          title: "操作",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "success",
                    icon: "ios-create-outline",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.showRoleModal(params.row);
                    }
                  }
                },
                "修改"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "success",
                    icon: "ios-cog-outline",
                    size: "small"
                  },
                  style: {
                    "margin-left": "4px"
                  },
                  on: {
                    click: () => {
                      this.permissionModal = true;
                    }
                  }
                },
                "设置权限"
              )
            ]);
          }
        }
      ],
      data1: [],
      permissionData: [
        {
          title: "医院管理",
          expand: true,
          children: [
            {
              title: "人员管理",
              expand: true,
              children: [
                {
                  title: "增加"
                },
                {
                  title: "修改"
                },
                {
                  title: "删除"
                },
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "部门管理",
              expand: true,
              children: [
                {
                  title: "增加"
                },
                {
                  title: "修改"
                },
                {
                  title: "删除"
                },
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "科室管理",
              expand: true,
              children: [
                {
                  title: "增加"
                },
                {
                  title: "修改"
                },
                {
                  title: "删除"
                },
                {
                  title: "查看当前机构"
                }
              ]
            }
          ]
        },
        {
          title: "废弃物管理",
          expand: true,
          children: [
            {
              title: "医废采集",
              expand: true,
              children: [
                {
                  title: "删除"
                },
                {
                  title: "查看当前机构"
                },
                {
                  title: "查看下属机构"
                }
              ]
            },
            {
              title: "医废装箱",
              expand: true,
              children: [
                {
                  title: "删除"
                },
                {
                  title: "查看当前机构"
                },
                {
                  title: "查看下属机构"
                }
              ]
            },
            {
              title: "医废出库",
              expand: true,
              children: [
                {
                  title: "删除"
                },
                {
                  title: "查看当前机构"
                },
                {
                  title: "查看下属机构"
                }
              ]
            },
            {
              title: "医废采集日报表",
              expand: true,
              children: [
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "医废采集月报表",
              expand: true,
              children: [
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "医废采集年报表",
              expand: true,
              children: [
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "医废采集流水报表",
              expand: true,
              children: [
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "医废小时报表",
              expand: true,
              children: [
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "装桶日统计报表",
              expand: true,
              children: [
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "装桶月统计报表表",
              expand: true,
              children: [
                {
                  title: "查看当前机构"
                }
              ]
            },
            {
              title: "装桶年统计报表",
              expand: true,
              children: [
                {
                  title: "查看当前机构"
                }
              ]
            }
          ]
        },
        {
          title: "系统管理",
          expand: true,
          children: [
            {
              title: "角色管理",
              expand: true,
              children: [
                {
                  title: "增加"
                },
                {
                  title: "修改"
                },
                {
                  title: "删除"
                },
                {
                  title: "查看当前机构"
                },
                {
                  title: "设置权限"
                }
              ]
            }
          ]
        }
      ]
    };
  },
  methods: {
    selectionChange(selection) {
      this.selectionIds = selection.map(item => item.roleId).join(",");
    },
    page(pageIndex = 1) {
      this.queryForm.pageIndex = pageIndex;
      this.$custom.post("api/role/getRoleList", this.queryForm, this, data => {
        this.data1 = data.result;
        this.dataTotal = data.total || this.dataTotal;
      });
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.saveRole();
          this.roleModal = false;
        } else {
          this.$Message.error("请填写完整");
        }
      });
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    showRoleModal(row = {}) {
      this.roleForm = this.$custom.handleForm(this.roleForm, row);
      this.isAdd = Object.keys(row).length === 0;
      this.roleModal = true;
    },
    showDelModal() {
      this.$custom.showDelModal(this, () => {
        this.delRole();
      });
    },
    saveRole() {
      let url = this.isAdd ? "api/role/addRole" : "api/role/updRole";
      this.roleForm.orgId = 1;
      this.$custom.post(url, this.roleForm, this, data => {
        this.page();
        this.$Message.success(data.msg);
      });
    },
    delRole() {
      this.$custom.post(
        "api/role/delRole",
        { ids: this.selectionIds },
        this,
        data => {
          this.page();
          this.$Message.success(data.msg);
        }
      );
    }
  },
  created() {
    this.page();
  }
};
</script>